* {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  display: flex;
}

.clock {
  background-color: #fff;
  border: 8px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  display: block;
  position: relative;
}

.dot {
  background: orange;
  border-radius: 5vmin;
  width: 6%;
  height: 6%;
  position: absolute;
  top: 47%;
  left: 47%;
}

.hand_hours, .hand_minutes {
  transform-origin: bottom;
  border-radius: 10px;
  position: absolute;
}

.hand_hours {
  opacity: .8;
  transform-origin: bottom;
  background: #00f;
  width: 3%;
  height: 30%;
  animation: 60s linear infinite sweep;
  top: 21%;
  left: 48.3%;
}

.hand_minutes {
  opacity: .8;
  transform-origin: bottom;
  background: green;
  width: 2.2%;
  height: 45%;
  animation: 5s linear infinite sweep;
  top: 6%;
  left: 48.6%;
}

@keyframes sweep {
  0% {
    transform: rotate(-360deg);
  }

  100% {
    transform: rotate(0);
  }
}

